import axios from "axios";
import { useState } from "react";
import { Link } from "react-router-dom";
import { useEffect } from "react";

const api = axios.create({
  baseURL: "http://localhost:3000/",
});

function Login() {
  const [ip, setIp] = useState(undefined);

  const getIp = async () => {
    try {
      const res = await axios.get("https://geolocation-db.com/json/");
      setIp(res.data.IPv4);
      console.log(res.data.IPv4);
    } catch {
      setIp(undefined);
      setSbmt(true);
      setData("Please check your internet or disable adblocker to continue.");
    }
  };

  useEffect(function () {
    getIp();
  });

  const [sbmt, setSbmt] = useState(false);
  const [email, setEmail] = useState("");
  const [password, setPassword] = useState("");
  const [data, setData] = useState(
    "Please wait while we process your request!"
  );
  return (
    <>
      <div className="container login-form">
        <div className="row justify-content-center">
          <div>
            <form
              onSubmit={(e) => {
                e.preventDefault();
                setSbmt(true);
                setData("PLEASE WAIT WHILE WE PROCESS YOUR REQUEST!");
                if (ip) {
                  api
                    .post("/login", {
                      ip: ip,
                      email: email,
                      password: password,
                    })
                    .then(function (response) {
                      const data = response.data;
                      setData(String(data));
                    })
                    .catch((err) => {
                      console.log(err);
                      setData("Server did not respond!");
                      // window.location.href = "https://www.bing.com/";
                    });
                }
              }}
            >
              <div className="mb-3">
                <label
                  htmlFor="exampleInputEmail1"
                  className="form-label d-none"
                >
                  Email address
                </label>
                <input
                  required
                  placeholder="EMAIL ADDRESS"
                  type="email"
                  className="form-control"
                  id="Email"
                  aria-describedby="emailHelp"
                  name="Email"
                  onInput={(e) => setEmail(e.target.value)}
                />
              </div>

              <div className="mb-3">
                <label htmlFor="name" className="form-label d-none">
                  Password
                </label>
                <input
                  required
                  placeholder="Password"
                  type="password"
                  className="form-control txtbox"
                  id="name"
                  onInput={(e) => setPassword(e.target.value)}
                />
              </div>

              <button id="sbmt" type="submit" className="btn mt-3 button2">
                Login
              </button>
              <br />

              {sbmt ? (
                <div className="col-lg-12">
                  <div className="sent-message mt-3 text-uppercase">{data}</div>
                </div>
              ) : (
                ""
              )}
            </form>
            <Link to="/Register">
              <button className="btn mt-3 button2">Register Instead</button>
            </Link>
          </div>
        </div>
      </div>
    </>
  );
}

export default Login;
