import axios from "axios";
import { useState } from "react";

const api = axios.create({
  baseURL: "http://localhost:3000/",
});

function Teacher() {
  const [regStatus, setRegStatus] = useState(-1);
  const [regUsers, setRegUsers] = useState([]);
  const [logStatus, setLogstatus] = useState(-1);
  const [logUsers, setLogUsers] = useState([]);

  return (
    <>
      <div className="container pt-5">
        <form
          onSubmit={(e) => {
            setRegStatus(0);
            e.preventDefault();
            api
              .post("/Registered")
              .then((response) => {
                setRegStatus(1);
                setRegUsers(response.data);
              })
              .catch((err) => {
                setRegStatus(-2);
              });
          }}
          className="mb-3"
        >
          <div>
            <button id="sbmt" type="submit" className={"btn button2 "}>
              Registered Users
            </button>
          </div>
          <div className="col-lg-12">
            <div className="sent-message">
              {regStatus === -1 ? (
                ""
              ) : regStatus === 0 ? (
                <div style={{ padding: "10px 10px" }}>PLEASE WAIT...</div>
              ) : regStatus === 1 ? (
                <>
                  <br />
                  {regUsers.length}
                  {regUsers.length === 1
                    ? " Registered User Found."
                    : " Registered Users Found."}
                  <br />
                  <br />
                  {regUsers.map((user) => (
                    <div key={user.email}>
                      <table
                        style={{
                          margin: "0 auto",
                          borderSpacing: "15px 0",
                          borderCollapse: "separate",
                        }}
                      >
                        <tr>
                          <td>Name</td>
                          <td>{user.name}</td>
                        </tr>
                        <tr>
                          <td>IP Address</td>
                          <td>{user.ip}</td>
                        </tr>
                        <tr>
                          <td>Email ID</td>
                          <td>{user.email}</td>
                        </tr>
                      </table>
                      <br />
                    </div>
                  ))}
                </>
              ) : (
                "Error!"
              )}
            </div>
          </div>
        </form>

        <form
          onSubmit={(e) => {
            e.preventDefault();
            setLogstatus(0);
            api
              .post("/LoggedIn")
              .then(function (response) {
                setLogstatus(1);
                setLogUsers(response.data);
              })
              .catch((err) => {
                setLogstatus(-2);
              });
          }}
        >
          <div>
            <button id="sbmt" type="submit" className={"btn button2 "}>
              Logged in Users
            </button>
          </div>
          <div className="col-lg-12">
            <div className="sent-message">
              {logStatus === -1 ? (
                ""
              ) : logStatus === 0 ? (
                <div style={{ padding: "10px 10px" }}>PLEASE WAIT...</div>
              ) : logStatus === 1 ? (
                <>
                  <br />
                  {logUsers.length}
                  {logUsers.length === 1
                    ? " Logged in User Found."
                    : " Logged in Users Found."}
                  <br />
                  <br />
                  {logUsers.map((user) => (
                    <div key={user.email}>
                      <table
                        style={{
                          margin: "0 auto",
                          borderSpacing: "15px 0",
                          borderCollapse: "separate",
                        }}
                      >
                        <tr>
                          <td>Name</td>
                          <td>{user.name}</td>
                        </tr>
                        <tr>
                          <td>IP Address</td>
                          <td>{user.ip}</td>
                        </tr>
                        <tr>
                          <td>Email ID</td>
                          <td>{user.email}</td>
                        </tr>
                      </table>
                      <br />
                    </div>
                  ))}
                </>
              ) : (
                "Error!"
              )}
            </div>
          </div>
        </form>
      </div>
    </>
  );
}

export default Teacher;
